
import { Fragment, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {containeImgFit} from '../../slices/courseSlice';
import _1_slidingPanels from '../../assets/js/components/_1_sliding-panels.js';

// Данный компонент: представляет собою меню-бургер, которое отличается от меню во всех прочих компонентах
//используем dispatch(containeImgFit() из courseSlice для того, чтобы менять свойство object-fit главной заставки. Таймер используется, чтобы исключить баг перерисовки

const HeaderInCourse = (state) => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    useEffect(()=>{
        
        _1_slidingPanels();
    },[])
    

    const componentUpdate = useSelector(state => state.courses.btn);
    const setButtonVisibility = (process) => {
        switch (process) {
            case 'true':
                // console.log(`Кнопка в Хедере: ${componentUpdate} Должна исчезнуть`);
                return {display: "none"};
            case 'false':
                // console.log("кнопка отзыа не кликалась");
                return {display: "flex"};
        }
    }
    let closeBtn = setButtonVisibility(componentUpdate);

    return (
        
        <Fragment>
            <button onClick={()=> [navigate("/courses"), setTimeout(()=>{dispatch(containeImgFit())},300) ]} style={closeBtn} className="reset s-panels__nav-control js-s-panels__nav-control js-tab-focus" aria-label="Toggle navigation">
                <svg className="icon icon--sm" viewBox="0 0 24 24" aria-hidden="true"><g className="icon__group" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"><line x1="2" y1="6" x2="22" y2="6" /><line x1="2" y1="12" x2="22" y2="12" /><line x1="2" y1="18" x2="22" y2="18" /></g></svg>
            </button>
            <div className="s-panels__nav-wrapper bg-contrast-higher font-smooth is-hidden js-s-panels__nav-wrapper">
                <div className="container height-100% padding-top-xxxl padding-bottom-md padding-y-md@sm">
                    <div className="grid height-100%">
                        <nav className="margin-bottom-md margin-bottom-0@sm col-6@sm flex@sm" aria-label="Main">
                        <ul className="flex flex-column gap-sm margin-top-auto@sm">
                            <li><a onClick={()=>{navigate("/")}}  className="s-panels__nav-link text-xxl text-xxxl@sm" to="/"> Главная</a></li>
                            <li><a onClick={()=>{navigate("/courses")}} className="s-panels__nav-link text-xxl text-xxxl@sm" to="courses">Курсы</a></li>
                            <li><a onClick={()=>{navigate("/contacts")}} className="s-panels__nav-link text-xxl text-xxxl@sm" href="contacts">Обратная связь</a></li>
                            <li><a onClick={()=>{navigate("/public-offer")}} className="s-panels__nav-link text-xxl text-xxxl@sm" to="public-offer">Публичная оферта</a></li>
                        </ul>
                        </nav>
                
                        <div className="margin-top-auto col-6@sm">
                        <ul className="flex flex-column gap-xs flex-row@sm justify-end@sm">
                            <li><a className="s-panels__social-link text-sm@sm" href="https://wa.me/79615157034">Whatsapp</a></li>
                            <li><a className="s-panels__social-link text-sm@sm" href="https://www.instagram.com/natka_lu/">Instagram</a></li>
                            {/* <li><a className="s-panels__social-link text-sm@sm" href="#0">Newsletter</a></li> */}
                        </ul>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}

export default HeaderInCourse;
