import { createSlice} from "@reduxjs/toolkit";
import HeaderInCourse from "../components/courses/HeaderInCouse";
const initialState = {
    btn: 'false',
    btnChief: 'false',
    objectFit: 'contain'
    
};
//ЗАДАЧА НА ПОНЕДЕЛЬНИК: СДЕЛАТЬ НЕ ИЗМЕНЕНИЕ ОБЪЕКТА А СДЕЛАТЬ action 
const courseSlice = createSlice({
    name: 'courses',
    initialState,
    reducers: {
        hiddenBtn(state) {state.btn='true'},
        showBtn(state) {state.btn='false'},
        containeImgFit(state){state.objectFit = 'contain'},
        coverImgFit(state){state.objectFit = 'cover'}
    },
        
    
})
const {actions, reducer} = courseSlice;

export default reducer;
export const {hiddenBtn, showBtn, deleteHiddenBtn, coverImgFit, containeImgFit} = actions;
