import {createSlice, createAsyncThunk, createEntityAdapter} from '@reduxjs/toolkit';
import {useHttp} from '../hooks/http.hook';

const feedbackAdapter = createEntityAdapter();
const initialState = feedbackAdapter.getInitialState({
    photoLoadingStatus: 'idle'
});

export const fetchPhoto = createAsyncThunk(
    'feedBackPhoto/fetchPhoto',
    (nameFolder) => {
        const {request} = useHttp();
        return request(`http://localhost:3001/${nameFolder}`);
    }
);

const feedBackSlice = createSlice({
    name: 'feedBackPhoto',
    initialState,
    reducers: {
        photoCreated: (state, action) => {
            feedbackAdapter.addOne(state, action.payload);
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchPhoto.pending, state => {state.photoLoadingStatus = 'loading'})
            .addCase(fetchPhoto.fulfilled,(state, action)=>{
                state.photoLoadingStatus = 'success';
                feedbackAdapter.setAll(state, action.payload);
            })
            .addCase(fetchPhoto.rejected, state => {
                state.photoLoadingStatus = 'error';
            })
            .addDefaultCase(() => {})
        }
})
const {reducer} = feedBackSlice;

export default reducer;
export const {selectAll} = feedbackAdapter.getSelectors(state => state.feedBackPhoto);

