const mail = (url, data, idResultMessage)=>{
    const resultText = document.getElementById(idResultMessage);
    const message = {
        loading: 'Загрузка...',
        success: 'Спасибо! Скоро я с вами свяжусь',
        failure: 'Что-то пошло не так...',
        // spinner: 'icons/messages/spinner.gif',
        // ok: 'icons/messages/ok.png',
    }
    const postData = async (url, data) => {
        let res = await fetch(url, {
            method: 'POST',
            body: data
        });
        return await res.text();
    }
    let statusMessage = document.createElement('div');
    // statusMessage.classList.add('status');
    resultText.appendChild(statusMessage);
    // const formData = new FormData(data);
    postData(url, data)
        .then(result =>{
            statusMessage.textContent = message.success;
        })
        .catch(() =>{
            statusMessage.textContent = message.failure;
        })
        .finally(()=>{
            setTimeout(()=>{
                statusMessage.remove();
            }, 2000);
        });
};
export default mail;