import {useHttp} from '../../hooks/http.hook';
import { useEffect, useState} from 'react';
import { useDispatch} from 'react-redux';
import { hiddenBtn, showBtn} from '../../slices/courseSlice';

import _3_lightbox from '../../assets/js/components/_3_lightbox.js';
import addScript from '../../hooks/addScript';
import removeScript from '../../hooks/removeScript';
import './feedback.scss';

const FeedBack =(props) => {
    const course = props.courseName; 
    const dispatch = useDispatch();
    const {request} = useHttp();
    const [contents, setContents] = useState(null);
    
    const getContent = (courseName) =>{
        request(`http://localhost:3001/${courseName}`)
         .then(data => setContents(feedBackElement(data)))
         .catch(err => console.log(err));
    }
    // useEffect(()=>{
    //     getContent(course);
    //     setTimeout(()=>{
    //         addScript('photo.js');
    //     }, 1000)
    // },[])
    useEffect(()=>{
        getContent(course);
        setTimeout(()=>{
            _3_lightbox();
        }, 1000)
    },[])

    
    const feedBackElement = (data)=>{
        const item = data.map(item=>{
            
            // console.log(item.id);
            const img = item.src;
            const select = (item.id==1) ? "slideshow__item--selected" : "";
           
            return (
                <>
                    <li key={item.id}  className={"slideshow__item bg js-slideshow__item " + select}>
                        <figure className="lightbox__media" data-media="img">
                            <div className="lightbox__media-outer js-lightbox__media-outer">
                                <div className="lightbox__media-inner js-lightbox__media-inner">
                                    <img data-src={process.env.PUBLIC_URL + img} data-thumb={process.env.PUBLIC_URL + img} src={process.env.PUBLIC_URL + img} alt="Image Info"/>
                                </div>
                            </div>
                            <figcaption className="lightbox__caption">Благодарственный отзыв</figcaption>
                        </figure>
                    </li>
                </>
            )
            
        });
        return (

            <ul key={111} className="slideshow__content js-lightbox__slideshow">
                <div className="modal modal--animate-fade lightbox js-modal js-lightbox" id="lightboxExample" data-modal-first-focus="#lightboxControllers">
                    <div className="lightbox__content">
                        <header className="lightbox__header">
                            <p className="lightbox__title">Отзывы Instagram</p>
                            {/* <menu onClick={()=> [dispatch(showBtn()), cleanScript("myScript")]}  */}
                            <menu onClick={()=> [dispatch(showBtn()), removeScript()]}  className="menu-bar menu-bar--expanded@md js-menu-bar menuVisible" data-menu-classname="menu--overlay">
                                <li  className="menu-bar__item js-modal__close" role="menuitem">
                                <svg className="icon menu-bar__icon" aria-hidden="true" viewBox="0 0 24 24"><g strokeLinecap="square" strokeLinejoin="miter" strokeMiterlimit="10" strokeWidth="2" stroke="currentColor" fill="none"><line x1="19" y1="5" x2="5" y2="19"></line><line x1="19" y1="19" x2="5" y2="5"></line></g></svg>
                                <span className="menu-bar__label reset">Close</span>
                                </li>
                            </menu>
                        </header>

                        <div className="lightbox__body slideshow slideshow--transition-fade js-lightbox__body" data-swipe="on" data-navigation="off">
                            <p className="sr-only">Modal Gallery Slideshow</p>
                            {item}
                            {/* {showContent} */}
                            <ul id="lightboxControllers">
                                <li className="slideshow__control js-slideshow__control">
                                <button className="reset slideshow__btn js-tab-focus">
                                    <svg className="icon" viewBox="0 0 32 32"><title>Предыдущий отзыв</title><path d="M20.768,31.395L10.186,16.581c-0.248-0.348-0.248-0.814,0-1.162L20.768,0.605l1.627,1.162L12.229,16 l10.166,14.232L20.768,31.395z"></path></svg>
                                </button>
                                </li>
                            
                                <li className="slideshow__control js-slideshow__control">
                                <button className="reset slideshow__btn js-tab-focus">
                                    <svg className="icon" viewBox="0 0 32 32"><title>Следующий отзыв</title><path d="M11.232,31.395l-1.627-1.162L19.771,16L9.605,1.768l1.627-1.162l10.582,14.813 c0.248,0.348,0.248,0.814,0,1.162L11.232,31.395z"></path></svg>
                                </button>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
              
            </ul>
        )  
    }



    
    console.log('render компонента FeedBack');
    return (
        <>
            <button onClick={()=> dispatch(hiddenBtn())} className="btn btn--primary text-xl shadow-md" aria-controls="lightboxExample">Наши отзывы</button>
            {contents}
        </>
    )
};
export default FeedBack;